<script lang="coffeescript">

  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  ###
    Future Consideration:
    [broken] https://github.com/weavy/quill-autoformat
    https://github.com/morishjs/quill-markdown-toolbar
    https://github.com/KMLX28/quill-find-replace-module
    https://github.com/UmbraEngineering/quilljs-renderer


  ###


  export toolbarOptions = [
    [{ header: 1 }, { header: 2 }, "blockquote", "link", "image", "video"]
    ["bold", "italic", "underline", "strike","code"]
    ["image","code-block"]
    [{ list: "ordered" }, { list: "ordered" }]
    [{ align: [] }]
    ["clean"]
  ]

  export placeholder = "Type something..."
  export content = undefined

  editorRef = undefined
  quill = undefined
  hljs.configure
    languages: ['javascript', 'ruby', 'python']

  onMount ()->

    quill = new Quill editorRef,
        modules:
          # syntax: true
          toolbar: toolbarOptions
          markdownShortcuts: {}
          table: true
          #tableUI: true
          #magicUrl:
          #  # Regex used to check URLs during typing
          #  urlRegularExpression: /(https?:\/\/[\S]+)|(www.[\S]+)|(mailto:[\S]+)|(tel:[\S]+)/,
          #  # Regex used to check URLs on paste
          #  globalRegularExpression: /(https?:\/\/|www\.|mailto:|tel:)[\S]+/g

          # table: false # disable table module
          #"better-table":
          #  operationMenu:
          #    items:
          #      unmergeCells:
          #        text: 'Another unmerge cells name'
          #keyboard:
          #  bindings: QuillBetterTable.keyboardBindings
        placeholder: placeholder
        theme: "bubble"


    quill.on "editor-change", (eventName, ...args)->
      html = editorRef.children[0].innerHTML
      text = editorRef.children[0].innerText
      changes = {
        type: "selection-change"
        html: html
        text: text
        event: eventName
        args: args
      }
      dispatch "change", changes

</script>
